.profile-full {
  font-family: 'Raleway', sans-serif;
  background-image: url(/static/media/background.9bd94859.jpg);
  background-size: cover;
  /* display: flex; */
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  padding: 20px;
  height: 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: 'center';
  background-size: 'cover'
}

.container-profile{
    margin: 0px auto;
    display: flex;
    justify-content: center;
    padding: 2em;
    margin:  auto;
    margin-bottom: 20px;
    overflow: auto;
    border-radius: 20px;
    width: 70vw;
    
    background:rgba(255,255,255,0.25);
    box-shadow : 0 8px 32px 0 rgba(31,38,135,0.37);
    -webkit-backdrop-filter : blur(8.5px);
            backdrop-filter : blur(8.5px);
}


.mainContainer-profile{
    display: flex;
    flex-direction: column;
    justify-content:baseline;
    margin:  auto;  
    overflow: hidden;
}

.blackLine-profile{
    margin-top: 1rem;
    width: 80%;
    height: 1px;
    background-color: rgb(79, 3, 102);
}
.blackLine-vertical{
    margin-top: 1rem;
    width: 10px;
    height: 90%;
    background-color: rgb(241, 13, 13);
}

.sidebar-img-profile{
    height: 50vh;
    display: block;
    /* border-radius: 3000px; */
}

.Button-profile{
    padding-left : 36rem;
    padding-bottom : 2.5rem;
    padding-top : 1rem;
}

.WelcomeText-profile{
    margin: 2rem 0 2rem 0;
}

#message {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
}
#inner-message {
    margin: 0 auto;
}
